@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{margin:0;box-sizing:border-box;font-family:'Roboto', sans-serif}

.App{padding:0 10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@media (min-width: 300px){.App{width:300px;margin:0 0 0 100px}}@media (min-width: 600px){.App{width:600px;margin:auto}}.App__title{text-transform:uppercase;font-size:20px;margin:20px 0}.App__button{display:block;width:150px;height:30px;font-size:16px;font-weight:bold;border:2px solid transparent;border-radius:10px;background-color:#b4defe;transition:all 0.3s}.App__button:hover{background-color:#fff;border-color:#b4defe;-webkit-transform:scale(1.1);transform:scale(1.1)}

.Chart{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}@media (min-width: 300px){.Chart{width:200px}}@media (min-width: 600px){.Chart{width:400px}}.Chart__item{width:100%;height:30px;background-color:#f4f4f4;margin:0 0 20px}.Chart__item::before{position:absolute;line-height:30px;font-size:16px;-webkit-transform:translateX(-100%);transform:translateX(-100%);padding:0 5px 0 0}.Chart__item--0::before{content:'Landing Page'}.Chart__item--1::before{content:'Configurator'}.Chart__item--2::before{content:'Check-out'}.Chart__item--3::before{content:'Deal'}.Chart__time{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#b4defe;height:100%}

